.contactanos_container {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    .contactUs_card {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        overflow: hidden;
        background-color: #ffffff;
    }

    .left-side {
        @include elem-flex(center, center);
        width: 40%;
        max-width: 700px;
        height: 100%;
        background-image: url("../../assets/imgs/home/banner01.jpg");
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        display: none;
    
        img {
            width: 50%;
        }
    }
    
    .rigth-side {
        @include elem-flex(center, center);
        flex-direction: column;
        flex: 1;
        padding: 50px 0;
        gap: 25px;
    
        h1 {
            font-size: 1.75rem;
            font-weight: 900;
        }
    
        p {
            width: 80%;
            text-align: center;
            display: none;
        }
    
        form {
            width: 100%;
            padding: 0 10px;
            display: flex;
            flex-direction: column;
            gap: 1rem;
            
            .oneGrid {
                grid-column: 1 / 3;
            }
            
            .halfLeftGrid {
                grid-column: 1 / 2;
            }
            
            .halfRigthGrid {
                grid-column: 2 / 3;
            }

            label {
                font-size: 0.85rem;
            }
        }
    
        .check-terms {
            @include elem-flex();
            width: 100%;
            padding: 0 10px;
            flex-direction: column;
            font-size: 0.9rem;
            gap: 10px;
            
            input {
                margin-right: 0.5rem;
            }
    
            a {
                color: var(--blue);
            }
        }
    
        button {
            @include btn-002(var(--red), false);
            border: none;
            padding: 0.75rem 2.5rem;
            border-radius: 50px;
        }
    }

    @media (min-width: 576px) {
        .contactUs_card {
            border-radius: 10px;
            @include card-shadow();
        }

        .rigth-side {
            h1 {font-size: 2.5rem;}
            p {display: inline;}
            form {
                width: 90%;
                display: grid;
                grid-template-columns: repeat(2, 1fr);
                grid-template-rows: repeat(4, auto);
            }
            .check-terms {
                width: 90%;
            }
        }
    }

    @media (min-width: 768px) {
        .rigth-side {
            form {
                width: 80%;
            }
            .check-terms {
                width: 80%;
            }
        }
    }

    @media (min-width: 1024px) {
        height: 100%;

        .contactUs_card {
            border-radius: 0;
            box-shadow: none;
        }

        .left-side {
            display: flex;
        }
    }

    @media (min-width: 1280px) {
        max-height: 750px;

        .contactUs_card {
            border-radius: 10px;
            @include card-shadow();
        }
    }
}